<template>
	<div>
		<div class="lg-line" :class="{centralScope: id == -1, centralScope2: id != -1}">
			<div class="paperScope lg-column">
				<div class="close">
					<img src="img/x.png" class="" @click="Close" />
				</div>

				<div class="titleScope lg-column lg-justifyCenter lg-alignCenter">
					<img src="img/police.png" alt="">
					<h2 class="vollkorn">{{translate.TR_POLICE_DEPARTMENT}}</h2>
				</div>

				<div class="informationScope lg-line lg-justifyBetween vollkorn">
					<p>{{translate.TR_REGISTRATION_NUMBER}}: {{record.id}}</p>
					<div class="timerScope lg-line">
						<p>{{record.date}}</p>
						<p>{{record.hours}}</p>
					</div>
				</div>

				<div class="documentScope lg-flex">
					<div class="confirmationScope">
						<img class="confirmationAnim" src="img/confirmation.png" alt="" v-if="confirmation">
					</div>
					<div class="charData lg-line lg-alignCenter">
						<p class="vollkorn">{{translate.TR_NAME}}:</p>
						<p class="specialElite" v-if="!isAnonymous">{{record.name}}</p>
						<p class="specialElite" v-else>Anonymous</p>
					</div>

					<div class="charData lg-line lg-alignCenter">
						<p class="vollkorn">{{translate.TR_JOB}}:</p>
						<input class="specialElite" v-if="!isAnonymous" v-model="record.job" :placeholder="translate.TR_INFORM_JOB" :readonly="id != -1" spellcheck="false">
						<p class="specialElite" v-else>Uninformed</p>
					</div>

					<div class="charData lg-line lg-alignCenter">
						<p class="vollkorn">{{translate.TR_PHONE_NUMBER}}:</p>
						<input class="specialElite" v-if="!isAnonymous" v-model="record.number" :placeholder="translate.TR_INFORM_NUMBER" :readonly="id != -1" spellcheck="false">
						<p class="specialElite" v-else>Uninformed</p>
					</div>

					<div class="charData charDataReport lg-line">
						<!-- <p class="vollkorn">REPORT:</p> -->
						<p class="vollkorn">{{translate.TR_REPORT}}:</p>
						<textarea class="specialElite reportTextarea lg-flex" v-model="record.report" ref="reportTextarea" @input="ValidateTextArea" @focusin="RemovePlaceholder" @focusout="AddPlaceholder" :readonly="id != -1" spellcheck="false"></textarea>
					</div>

					<div class="checkBox first lg-line">
						<div class="inputCheckBox lg-line lg-alignCenter lg-justifyCenter" @click="SetAnonymous(!isAnonymous)" v-if="!hideAnonymous">
							<i class="fas fa-times" v-if="isAnonymous"></i>
						</div>
						<p class="vollkorn checkBoxText" @click="SetAnonymous(!isAnonymous)" v-if="!hideAnonymous">{{translate.TR_ANONYMOUS}}</p>
					</div>

					<div class="checkBox last lg-line" :class="{declareAnim: notDeclared}">
						<div class="inputCheckBox lg-line lg-alignCenter lg-justifyCenter" @click="SetDeclaration(!hasDeclaration)">
							<i class="fas fa-times" v-if="hasDeclaration"></i>
						</div>
						<p class="vollkorn checkBoxText" @click="SetDeclaration(!hasDeclaration)">{{translate.TR_DECLARATION}}</p>
					</div>

					<div class="signaturesScope lg-line" :class="{['lg-justifyBetween']: !singleSignature, ['lg-justifyCenter']: singleSignature}">
						<div class="signature">
							<p class="dancingScript" v-if="!isAnonymous">{{record.name}}</p>
							<p class="dancingScript" v-else>Anonymous</p>
							<p class="vollkorn">{{translate.TR_DECLARANT}}</p>
						</div>

						<div class="signature" v-if="!singleSignature">
							<p class="dancingScript">{{translate.TR_NAME_DIRECTOR}}</p>
							<p class="vollkorn">{{translate.TR_DIRECTOR}}</p>
						</div>
					</div>
				</div>
			</div>

			<div class="carimboScope lg-column lg-flex" v-if="id == -1">
				<div class="groupCarimbo" @click="FinishRecord">
					<img class="carimbo" src="img/carimbo.png" alt="">
					<div class="finished lg-column lg-alignCenter lg-justifyCenter">
						<!-- <img class="finished" src="img/finished.png" alt=""> -->
                        <p class="carterOne">{{translate.TR_FINALIZE}}</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios';

	export default {
		name: "RecordView",

		props: {
			id: {},
		},

		data: function () {
			return {
				intervalTimestamp: null,

				textBackup: "",
				scrollHeight: 0,
                hideAnonymous: this.$store.state.hideAnonymous,
                singleSignature: this.$store.state.singleSignature,
                confirmation: false,

				record: {
                    id: 22,
                    name: "Your Name",
					hours: "00:00",
                    job: "",
                    number: "",
					date: "01/12/2000",
					report: "                  ",
                    anonymous: false,
				},

				isAnonymous: false,
				hasDeclaration: false,
                notDeclared: false,

				translate: this.$store.state.translate,
			};
		},

		methods: {
			SetAnonymous: function (value) {
				if (this.id == -1)
					this.isAnonymous = value;
			},

			SetDeclaration: function (value) {
				if (this.id == -1)
					this.hasDeclaration = value;
			},

			ValidateTextArea: function () {
				let init = "                  ";
				if (this.record.report.substring(0, init.length) != init) {
					while (this.record.report[0] == " ") this.record.report = this.record.report.slice(1);

					this.record.report = `${init}${this.record.report}`;
				}

				if (this.$refs.reportTextarea.scrollHeight > this.scrollHeight) {
					this.record.report = this.textBackup;
				}

				this.textBackup = this.record.report;
			},

			RemovePlaceholder: function () {
				let init = "                  ";
				let placeholder = `${init}${this.translate.TR_WRITE_REPORT}`;
				if (this.record.report == placeholder) {
					this.record.report = init;
				}
			},

			AddPlaceholder: function () {
				let init = "                  ";
				let placeholder = `${init}${this.translate.TR_WRITE_REPORT}`;
				if (this.record.report == init) {
					this.record.report = placeholder;
				}
			},

			GetRecord: function (recordId) {
				return this.$store.state.records.filter(record => record.id == recordId)[0];
			},

			LoadDocumentById: function (recordId) {
				let record = this.GetRecord(recordId);

				this.record = record;

				this.isAnonymous = record.anonymous;
				this.hasDeclaration = true;
                this.confirmation = true;
			},

            LoadInformation: function(){
                this.record.name = this.$store.state.nameReport;
                this.record.id = this.$store.state.nextId;
            },

			GetNow: function () {
				const today = new Date();
				let nowHours = "" + today.getHours();
				let nowMinutes = "" + today.getMinutes();
				let nowSeconds = "" + today.getSeconds();

				let nowMonth = "" + (today.getMonth() + 1);
				let nowDay = "" + today.getDate();
                let dateFormat = this.$store.state.dateFormat;

				if (nowHours.length == 1)
					nowHours = `0${nowHours}`;

				if (nowMinutes.length == 1)
					nowMinutes = `0${nowMinutes}`;

				if (nowSeconds.length == 1)
					nowSeconds = `0${nowSeconds}`;

				if (nowMonth.length == 1)
					nowMonth = `0${nowMonth}`;

				if (nowDay.length == 1)
					nowDay = `0${nowDay}`;

				this.record.date = dateFormat.replace("%d", nowDay).replace("%m", nowMonth).replace("%y", today.getFullYear());
				this.record.hours = nowHours + ":" + nowMinutes + ":" + nowSeconds;
			},

            FinishRecord: function(){
                if(this.confirmation)
                    return;
                
                if(!this.hasDeclaration){
                    this.notDeclared = true;
                    setTimeout(() => this.notDeclared = false, 2000);
                    return;
                }

                this.record.anonymous = this.isAnonymous;

                if(this.isAnonymous)
                    this.record.name = this.translate.TR_ANONYMOUS;

                this.SendToClient("FinishRecord", {
                    record: this.record,
                });
            },

            Close: function () {
                this.SendToClient("Close", {});
				this.$store.commit("SetOpenned", false);
                this.$router.replace({path: "/"});
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.nameResource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (RecordView): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
				if (lua.confirmRecord) {
					this.confirmation = true;
                    setTimeout(() => {
                        this.Close();
                    }, 2000);
				}
			},
            
		},

		mounted: function () {
			if (this.id == -1) {
				this.intervalTimestamp = setInterval(this.GetNow, 1000);
                this.AddPlaceholder();
                this.LoadInformation();
			} else {
				this.LoadDocumentById(this.id);
			}

			this.scrollHeight = this.$refs.reportTextarea.scrollHeight;
		},

		unmounted: function () {
			clearInterval(this.intervalTimestamp);

            window.removeEventListener("message", this.ReceiveLua);
			window.removeEventListener("keydown", this.KeyPress);
		},

        created: function () {
			window.addEventListener("message", this.ReceiveLua);
			window.addEventListener("keydown", this.KeyPress);
		},
	};
</script>

<style scoped>
	.close {
		position: absolute;
		top: 1%;
		right: 3%;
		transform: translate(3%, -1%);
	}

    .close img{
        width: 2vh;
        /* height: 2vh; */
    }

	.centralScope {
		position: absolute;
		left: 65%;
		top: 50%;
		transform: translate(-65%, -50%);
	}

	.centralScope2 {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}

	.paperScope {
		width: 75vh;
		height: 95vh;

		background-image: url("~@/assets/texture.jpg");
		/* background-size: 70px 70px; */
		background-position: 10px 0px;
		background-size: auto 100%;

		position: relative;
	}

	.titleScope {
		width: 100%;
		padding-top: 2vh;
	}

	.titleScope img {
		width: 8vh;
	}

	.titleScope h2 {
		font-size: 2vh;
		text-decoration: underline;
	}

	.documentScope {
		border: 1px solid black;

		margin: 0vh 2vh 2vh 2vh;
		position: relative;
	}

	.informationScope {
		padding: 0 3vh;
		margin-top: 2vh;

		font-size: 1.8vh;
		font-weight: 600;
	}

	.timerScope p {
		padding-left: 3vh;
	}

	.charData {
		margin: 4vh 0;
		font-size: 2vh;
		padding-left: 2vh;
		padding-right: 1vh;
	}

	.charData .vollkorn {
		/* margin-left: 2vh; */
		font-weight: 600;
	}

	.charData .specialElite {
		/* margin-left: 2vh; */
		padding-left: 3vh;
	}

	.charData input {
		padding-left: 3vh;
		font-size: 2vh;
		background: none;
		outline: none;
		border: none;
	}

	.charDataReport {
		width: 100%;
		position: relative;
		/* background: brown; */
	}

	.charDataReport .specialElite {
		padding-left: 0vh;
	}

	.charDataReport .vollkorn {
		position: absolute;
	}

	.reportScope {
		min-height: 14vh;
		max-height: 14vh;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.reportTextarea {
		outline: none;
		font-size: 2vh;

		background: none;
		border: none;
		height: 13vh;
		resize: none;

		line-height: 2.5vh;
		overflow: hidden;
	}

	.checkBox {
		margin-left: 3vh;
		margin-right: 1vh;
		margin-top: 4vh;
	}

	.checkBox.first {
		margin-top: 6vh;
	}

	.checkBoxText {
		margin-left: 2vh;
		font-size: 2vh;
		font-weight: bold;
	}

	.checkBox i {
		font-size: 2vh;
		color: rgba(0, 0, 0, 0.74);
	}

	.inputCheckBox {
		font-size: 2vh;
		border: 2px solid rgba(0, 0, 0, 0.678);
		min-width: 2vh;
		max-width: 2vh;
		max-height: 2vh;
		min-height: 2vh;
	}

	.signaturesScope {
		margin: 6vh 2vh 0vh 2vh;
	}

	.signature {
		text-align: center;
		font-size: 2vh;
		font-weight: bold;
	}

	.signature p:first-child {
		font-size: 2.4vh;
		border-bottom: 1px solid black;
		width: 25vh;
		margin-bottom: 1vh;
	}

	.confirmationScope {
		position: absolute;

		right: 18%;
		top: 10%;
		transform: translate(18%, -10%);

		/* width: 20vh; */
	}
	.confirmationAnim {
		width: 13vh;
		rotate: -25deg;
		animation: confirmationAnim 0.5s;
	}

	@keyframes confirmationAnim {
		0% {
			transform: scale(10);
		}
		100% {
			transform: scale(1);
		}
	}

	.carimboScope {
		padding-left: 5vh;

		position: relative;
		justify-content: end;
		margin-bottom: 10vh;
	}

	.carimboScope > div {
		position: relative;
	}

	.carimboScope .carimbo {
		width: 27vh;
		rotate: -25deg;
	}

	.finished {
		position: absolute;
		top: 75%;
		left: 10%;
		transform: translate(-10%, -75%);

		width: 13vh;
	}

	.groupCarimbo {
		transition: 0.3s;
	}

	.groupCarimbo:hover {
		transform: scale(1.1);
	}

    .finished{
        position: absolute;
        top: 60%;
        left: 20%;
        transform: translate(-20%, -60%);

        rotate: -23deg;
        /* max-width: 10vh; */
        width: 11vh;
        height: 5vh;
        padding: 1vh;

        background-image: url("~@/assets/carimbo_border.png");
        background-size: 100% 100%;
        /* background-color: white; */
        background-repeat: no-repeat;
    }

    .finished p{
        color: white;
        /* font-size: 2vh; */
        text-overflow: ellipsis;
        max-width: 10vh;
        overflow: hidden;
    }

    .declareAnim{
        animation: declareAnim 1s;
    }

    @keyframes declareAnim{
        0%{
            transform: scale(1.0);
        } 
        50%{
            transform: scale(1.1);
        }        
        100%{
            transform: scale(1.0);
        }
    }
</style>