<template>
	<div>
		<div class="screenScope lg-column">
			<div class="close">
				<img src="img/x.png" class="" @click="Close" />
			</div>
			<div class="titleScope lg-line">
				<img src="img/police.png" alt="">
				<div class="titleText lg-column lg-justifyCenter">
					<h1 class="leagueSpartan">{{translate.TR_POLICE_DEPARTMENT}}</h1>
					<h2 class="vollkorn">{{translate.TR_SUBTITLE}}</h2>
				</div>
			</div>

			<div class="barScope lg-line">
				<div class="directoryScope lg-line lg-flex lg-alignCenter">
					<i class="far fa-folder-open"></i>
					<p class="montserratLight"><span>></span>{{translate.TR_THIS_COMPUTER}}<span>></span>{{translate.TR_REGISTERED_REPORTS}}</p>
				</div>

				<div class="searchScope lg-line lg-alignCenter">
					<i class="fas fa-search fa-flip-horizontal"></i>
					<input type="text" class="montserratLight" :placeholder="translate.TR_SEARCH" v-model="search">
				</div>
			</div>

			<div class="listFolders lg-line lg-hideScroll" v-if="records.filter((item) => item.name.toLowerCase().includes(search.toLowerCase()) || item.id == search).length > 0">
				<div class="folderScope" v-for="(record, index) in records.filter((item) => item.name.toLowerCase().includes(search.toLowerCase()) || item.id == search)" :key="`record_${index}`" @click="OpenRecord(record.id)">
					<p class="leagueSpartan number">{{record.id}}</p>
					<img src="img/folder.png" alt="">
					<p class="montserratLight name">{{record.anonymous ? translate.TR_ANONYMOUS : record.name}}</p>
				</div>
			</div>

            <div class="listEmpty lg-line lg-justifyCenter lg-alignCenter lg-flex" v-else>
                <p class="montserratLight">{{translate.TR_NO_RECORD_FOUND}}</p>
            </div>
		</div>
		<div class="computerScope lg-posCenter">

		</div>
	</div>
</template>

<script>
	import axios from "axios";

	export default {
		name: "ComputerView",

		data: function () {
			return {
				search: "",
				records: this.$store.state.records,
                translate: this.$store.state.translate,
			};
		},

		methods: {
			OpenRecord: function (recordId) {
				if (recordId) {
					this.$router.push({ name: "RecordView", params: { id: recordId } });
				}
			},

            Close: function () {
				this.SendToClient("Close", {});
				this.$store.commit("SetOpenned", false);
			},

			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.nameResource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (ComputerView): ${to} : ${msg}`)
					);
			},

			// ReceiveLua: function (event) {
			// 	if (!event || !event.data) return;
			// 	const lua = event.data;
			// 	if (lua.loadRecords) {
			// 		this.$store.commit("SetRecords", lua.records);
			// 	}
			// },
		},

		mounted: function () {
			// 
		},

		unmounted: function () {
			// 
		},
	};
</script>

<style scoped>
	.close {
		position: absolute;
		top: 4%;
		right: 2%;
		transform: translate(2%, -4%);
        
        /* background-color: white; */
	}

    .close img{
        width: 2vh;
        filter: invert(100%);
        /* height: 2vh; */
    }

	.computerScope {
		width: 100vh;
		height: 100vh;
		background-image: url("~@/assets/monitor.png");
		background-repeat: no-repeat;
		background-size: 100% auto;
		background-position: center;

		pointer-events: none;

		/* background-color: antiquewhite; */
	}

	.screenScope {
		width: 94.6vh;
		height: 53.5vh;

		background-color: #f4f4f4;

		position: absolute;
		top: 31%;
		left: 50%;
		transform: translate(-50%, -31%);

		/* border: 1px solid rgb(138, 138, 138); */
		/* opacity: 0.5; */
	}

	.titleScope {
		background: black;
		padding: 1vh 3vh;
	}

	.titleScope img {
		width: 6vh;
	}

	.titleScope .titleText {
		padding-left: 1vh;
	}

	h1 {
		color: white;
		font-size: 2.9vh;
	}

	h2 {
		color: rgba(255, 255, 255, 0.877);
		font-weight: 400;
		font-size: 2.5vh;
	}

	.directoryScope {
		color: #828282;
		border: 1px solid #b4b4b4;
		margin: 3vh;
		margin-bottom: 0;
		padding: 0.5vh 0.3vh;
		font-size: 1.5vh;
        /* padding-top: 1vh; */
	}

	.directoryScope i {
		padding-left: 1vh;
	}

	.directoryScope span {
		padding: 0 1vh;
	}

	.searchScope {
		color: #828282;
		border: 1px solid #b4b4b4;
		margin: 3vh;
		margin-bottom: 0;
		margin-left: 0;
		padding: 0.5vh 0.3vh;
        /* padding-top: 1vh; */
		font-size: 1.5vh;
	}

	.searchScope input {
		background: none;
		border: none;
		outline: none;
		font-size: 1.4vh;
        padding: 0;
		/* padding: 0.3vh; */
		color: #828282;
	}

	.searchScope i {
		padding-right: 0.5vh;
		padding-left: 1vh;
	}

	.listFolders {
		margin: 3vh;
		margin-top: 1vh;
		margin-bottom: 0;
		padding-top: 3vh;
		padding-bottom: 1vh;
		/* background: black; */
		width: calc(100%-6vh);
		overflow: hidden;
		overflow-y: scroll;

		flex-wrap: wrap;
	}

	.folderScope {
		position: relative;
		/* background: black; */
		width: calc(100% / 7 - 2vh + 2vh / 7);
		margin-bottom: 2vh;
	}

	.folderScope:not(:nth-child(7n)) {
		margin-right: 2vh;
	}

	.folderScope .number {
		color: #fcd03a;
		position: absolute;
		top: 5%;
		left: 13%;
		transform: translate(-13%, -5%);
		font-size: 1.5vh;
		font-weight: bold;
        width: 3.5vh;
        /* background: #b4b4b4; */
        overflow: hidden;
        text-overflow: ellipsis;
	}

	.folderScope img {
		width: 100%;
	}

	.folderScope img:active {
		opacity: 0.9;
	}

	.folderScope .name {
		font-size: 1.5vh;
		text-align: center;
        width: 90%;
        margin: 0 auto;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        /* background-color: gray; */
	}

    .listEmpty{
        color: #828282;
        font-size: 2vh;
        /* font-style: italic; */
    }
</style>