<template>
	<div v-if="this.$store.state.openned">
		<router-view />
	</div>
</template>

<script>
	import axios from "axios";

	export default {
        data: function(){
            return({
                
            });
        },
		methods: {
			SendToClient: function (to, data) {
				axios
					.post(`https://${this.$store.state.nameResource}/${to}`, data)
					.catch((msg) =>
						console.log(`An error has occurred (Main): ${to} : ${msg}`)
					);
			},

			ReceiveLua: function (event) {
				if (!event || !event.data) return;
				const lua = event.data;
                if(lua.config){
                    this.$store.commit("SetTranslation", lua.translate);
                    this.$store.commit("SetNameResource", lua.namResource);
                    this.$store.commit("SetSingleSignature", lua.singleSignature);
                    this.$store.commit("SetHideAnonymous", lua.hideAnonymous);
                    this.$store.commit("SetDateFormat", lua.dateFormat);
                }
				else if (lua.openRecord) {
                    this.$store.commit("SetNameReport", lua.information.nameReport);
                    this.$store.commit("SetNextId", lua.information.nextId);
					this.$router.replace({name: "RecordView", params: {id: -1}});                    
                    this.$store.commit("SetOpenned", true);
				} else if (lua.openComputer) {
                    this.$store.commit("SetOpenned", true);
					this.$store.commit("SetRecords", lua.records);
					this.$router.push({name: "ComputerView"});
				}
			},

			Close: function () {
				this.SendToClient("Close", {});
				this.$store.commit("SetOpenned", false);
                this.$router.replace({path: "/"});
			},
			KeyPress: function (event) {
				const key = event.key;
				if (key == "Escape") {
					this.Close();
				}
			},

		},

		unmounted: function () {
			window.removeEventListener("message", this.ReceiveLua);
			window.removeEventListener("keydown", this.KeyPress);
		},

		created: function () {
			window.addEventListener("message", this.ReceiveLua);
			window.addEventListener("keydown", this.KeyPress);
		},
	}
</script>

